body {
  margin: 0;
  padding: 2% 4%;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h2 {
  margin-top: 0;
}

button {
  border: none;
  border-radius: 4px;
  padding: 10px 18px;
  transition: all 0.2s ease;
  border: 1px solid transparent;
}

button:hover {
  background: #e6e6e6;
  border: 1px solid grey;
}

button.voted {
  background-color: #4587ff;
  color: #fff;
}

button[disabled] {
  pointer-events: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  padding-bottom: 10px;
}

.login {
  display: flex;
  justify-content: center;
}

.login .wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 320px;
  margin: auto 0;
}

.login input {
  display: block;
  width: 100%;
  height: 30px;
  margin-bottom: 10px;
}

.login button {
  width: 100%;
}

.voting-view {
  display: flex;
  justify-content: space-between;
}

.box {
  width: 44%;
  margin: 20px 0;
  padding: 20px;
  border: 1px solid grey;
  border-radius: 10px;
}

.options__buttons button {
  width: 44%;
  margin-right: 20px;
  margin-bottom: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  font-size: 16px;
}

.user-list .user {
  width: 240px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user-list button {
  margin-top: 20px;
}

.user-list .result-button {
  margin-right: 10px;
}
